import React, { useState } from 'react';
import { Container, Row, Col, Button, Pagination, Spinner } from '@themesberg/react-bootstrap';


const ButtonGridWithPagination = ({ data, isLoading, onItem, nextLink, prevLink, currentPage, totalPages }) => {
    const allButtons = data.map((item, i) => ({
        id: i + 1,
        text: item.num + ' ' + (item?.sourceId || ""),
        variant: ['primary', 'secondary', 'success', 'danger', 'warning', 'info', 'dark'][i % 7]
    }));

    return (
        <Container className="mt-4">
            {/* Loading state */}
            {isLoading ? (
                <div className="text-center py-5">
                    <Spinner animation="border" role="status">
                        <span className="visually-hidden">Loading...</span>
                    </Spinner>
                    <p className="mt-2">Loading buttons...</p>
                </div>
            ) : (
                <>
                    {/* Button Grid */}
                    <Row xs={2} md={3} lg={4} className="g-4">
                        {allButtons.map((button) => (
                            <Col key={button.id}>
                                <Button
                                    variant={button.variant}
                                    className="w-100 py-3"
                                    onClick={e => onItem && onItem(button)}
                                >
                                    {button.text}
                                </Button>
                            </Col>
                        ))}
                    </Row>

                    {/* Simplified Pagination - Only Prev/Next */}
                    <div className="d-flex justify-content-center mt-4 align-items-center" style={{ verticalAlign: "center" }}>

                        <Pagination className="m-0">
                            {
                                prevLink && <Pagination.Prev
                                    as="a"
                                    href={prevLink}
                                />
                            }
                            <Pagination.Item>
                                Page {`${Number(currentPage) + 1}`} of {totalPages}
                            </Pagination.Item>
                            {
                                nextLink && <Pagination.Next
                                    as="a"
                                    href={nextLink}
                                />
                            }
                        </Pagination>


                    </div>
                </>
            )}
        </Container>
    );
};

export default ButtonGridWithPagination;